* {
    box-sizing: border-box;
    z-index: 0;
}
html {
    background-color: #fff;
}
.wrapper {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow-x: hidden;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
}
body {
    font-family: 'Manrope', sans-serif;
    font-weight: 400;
    font-style: normal;
    font-size: 18px;
    color: #081533;
    line-height: 1.333;
}
img {
    max-width: 100%;
    display: block;
}
a {
    text-decoration: none !important;
    color: inherit;
}
h1,
h2,
h3,
h4,
p {
    margin: 0;
}
//grid
.section {
    padding-top: 2em;
    padding-bottom: 4em;
}
.section__content {
    & + .section__content {
        margin-top: 2em;
    }
    .section__content {
        margin-top: 2em;
    }
}
.item-center {
    margin: 0 auto;
}
.grid {
    position: relative;
    margin-left: auto;
    margin-right: auto;
    padding-left: 15px;
    padding-right: 15px;
    width: 100%;
    z-index: 200;
    max-width: 1200px;
}
.grid__row {
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin-left: -15px;
    margin-right: -15px;
}
//justify-content
.grid__row._lg_content_between {
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
}
.grid__row._lg_content_around {
    justify-content: space-around;
}
.grid__row._lg_content_start {
    justify-content: flex-start;
}
.grid__row._lg_content_end {
    justify-content: flex-end;
}
.grid__row._lg_content_center {
    justify-content: center;
}
//align-items
.grid__row._lg_items_end {
    -webkit-box-align: end;
    -ms-flex-align: end;
    align-items: flex-end;
}
.grid__row._lg_items_start {
    align-items: flex-start;
}
.grid__row._lg_items_center {
    align-items: center;
}
.grid__row._lg_items_stretch {
    align-items: stretch;
}
.grid__col {
    padding-left: 15px;
    padding-right: 15px;

    &._col_flex {
        display: flex;
        align-items: flex-start;
        justify-content: center;
        flex-direction: column;
    }
}
.grid__col._lg_1 {
    width: 8.33333%;
}
.grid__col._lg_2 {
    width: 16.66667%;
}
.grid__col._lg_3 {
    width: 25%;
}
.grid__col._lg_4 {
    width: 33.33333%;
}
.grid__col._lg_5 {
    width: 41.66667%;
}
.grid__col._lg_6 {
    width: 50%;
}
.grid__col._lg_7 {
    width: 58.33333%;
}
.grid__col._lg_8 {
    width: 66.66667%;
}
.grid__col._lg_9 {
    width: 75%;
}
.grid__col._lg_10 {
    width: 83.33333%;
}
.grid__col._lg_11 {
    width: 91.66667%;
}
.grid__col._lg_12 {
    width: 100%;
}
//text defaulte
.text {
    &-center {
        text-align: center;
    }
    &-right {
        text-align: right;
    }
    &-bold {
        font-weight: 700;
    }
    &-semi {
        font-weight: 500;
        font-style: normal;
    }
    &-normal {
        font-weight: normal;
        font-style: normal;
    }
    &-italic {
        font-style: italic;
    }
    &-transform {
        text-transform: uppercase;
    }
    &-justify {
        text-align: justify;
    }
    &-white {
        color: #fff;
    }
    &-blue {
        color: #143989;
    }
    &-dark {
        color: #384258;
    }
    &-light {
        color: #303030;
    }
    // &-pink {
    //     color: #DC0086;
    // }
}
.margin-t-mini {
    margin-top: 0.5em;
}
.margin-t-max {
    margin-top: 1.5em;
}
.margin-t {
    margin-top: 1em;
}
.margin-b-mini {
    margin-bottom: 0.5em;
}
.margin-b-max {
    margin-bottom: 1.5em;
}
.margin-b {
    margin-bottom: 1em;
}
.text {
    //55
    &-title {
        font-size: 2.778em;
        line-height: 1.13em;
    }
    //34
    &-subtitle {
        font-size: 1.889em;
    }
    //30
    &-subtitle-mini {
        font-size: 1.667em;
    }
    //24
    &-st {
        font-size: 1.333em;
    }
    //22
    &-st-mini {
        font-size: 1.222em;
    }
    //16
    &-litr {
        font-size: 0.889em;
    }
    //12
    &-litr-mini {
        font-size: 0.666em;
    }
}

._1 {
    background: url(/public/1_bg.png) top right 10% / auto no-repeat,
    #FFF2FA;
    .box.pink {
        background: url(../../public/1_box.png) bottom center / cover no-repeat;
    }
}
.box {
    padding: 4em 5.3em;
    color: #fff;

    &.pink {
        background-color: #DC0086;
        margin-top: 2em;
        margin-bottom: 4em;
    }
    &.lightblue {
        background: #E7EEFB;
        border-left: 4px solid #143989;
    }
    &.blue {
        background: #143989;
    }
}
.box-mini {
    padding: 2em 3.5em;
    color: #fff;
    background: #143989;
}
ul {
    margin: 0;
    padding-left: 1.2em;

    li {
        margin-bottom: 1em;
    }

    &.circle {
        margin-right: 2em;
        list-style-type: circle;
    
        &.pink {
            color: #DC0086;
            li {
                margin-bottom: 1em;
                span {
                    color: #081533 !important;
                }
            }
        }
    }
    &.img {
        list-style-image: url(../../public/li_pink.png);
        li {
            margin-bottom: 1em;
        }
    }
}
img.graph {
    margin-right: 0;
    margin-left: auto;
}
._2 {
    padding-top: 2em !important;
    .box-mini {
        background: #FFF2FA;
        text-align: center;
        padding: 1.5em 0.5em;
        min-height: 220px;
        img {
            margin: auto;
            margin-bottom: 0.5em;
        }
    }
}
._3 {
    background-color: #FFF2FA;
    .box.pink {
        margin-bottom: 0;
    }
}
._4 {
    .box-mini {
        padding-left: 2em;
        padding-right: 1em;
    }
    .box.lightblue {
        margin-top: 4em;
        padding: 4em;
    }
}
._5 {
    background-color: #FFF2FA;

    .text-subtitle.margin-t {
        padding-right: 3em;
    }
    .box.pink {
        margin-bottom: 0;
        .text-st {
            padding: 0 2em;
        }
        img {
            margin-bottom: 0.5em;
        }
    }
}
._6 {
    ul {
        margin-right: 0;
    }
    .box.lightblue {
        margin-top: 4em;
        margin-bottom: 4em;
        padding: 4em;
    }
}
._7 {
    background: url(/public/7_bg.png) top right 10% / auto no-repeat,
    #FFF2FA;

    .box-mini {
        text-align: center;
        background-color: #fff;
        margin-top: 2em;
        min-height: 215px;
        img {
            margin: 0 auto 0.5em;
        }
    }
}
._8 {
    .text-litr-mini {
        line-height: 1.7;
    }
    .box-disappear {
        a {
            color: #303030;
            width: 350px;
            display: block;
            position: relative;
            box-sizing: border-box;
            text-align: center;
            text-transform: uppercase;

            &::before {
                content: '';
                background: url(../../public/arrow.png) center / contain no-repeat;
                width: 24px;
                height: 15px;
                position: absolute;
                top: 38%;
                right: 6%;
            }
            &.button_disappear::before {
                transform: rotate(180deg);
            }
        }

        .text-disappear {
            display: none; 
        }
        .button_disappear {
            display: none;
        }

        &.active {
            .text-disappear {
                display: block;
            }
            .button_disappear {
                display: block;
            }
            .button_red {
                display: none;
            } 
        }
        
    }
}


@media (max-width: 1100px) {
    .box {
        padding: 3em;
    }
    ._2 {
        .box-mini {
            min-height: 270px;
        }
    }
}
@media (max-width: 768px) {
    .section__content .section__content {
        margin-top: 1em;
    }
    .grid__col._sm_12 + .grid__col._sm_12 {
        margin-top: 1em;
    }
    .grid__row._sm_reverse {
        flex-direction: row-reverse;
    }
    .grid__row._sm_reverse_col {
        flex-direction: column-reverse;
    }
    .grid__row._sm_reverse .text-right {
        text-align: left;
    }
    .grid__row._sm_content_center {
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        justify-content: center;
    }
    .grid__row._sm_items_start {
        -webkit-box-align: start;
        -ms-flex-align: start;
        align-items: flex-start;
    }
    .grid__row._sm_items_end {
        -webkit-box-align: end;
        -ms-flex-align: end;
        align-items: flex-end;
    }
    .grid__row._sm_items_center {
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
    }
    .grid__row._sm_items_center {
        align-items: space-between;
    }
    .grid__col._sm_1 {
        width: 8.33333%;
    }
    .grid__col._sm_2 {
        width: 16.66667%;
    }
    .grid__col._sm_3 {
        width: 25%;
    }
    .grid__col._sm_4 {
        width: 33.33333%;
    }
    .grid__col._sm_5 {
        width: 41.66667%;
    }
    .grid__col._sm_6 {
        width: 50%;
    }
    .grid__col._sm_7 {
        width: 58.33333%;
    }
    .grid__col._sm_8 {
        width: 66.66667%;
    }
    .grid__col._sm_9 {
        width: 75%;
    }
    .grid__col._sm_10 {
        width: 83.33333%;
    }
    .grid__col._sm_11 {
        width: 91.66667%;
    }
    .grid__col._sm_12 {
        width: 100%;
    }
    ._sm_0 {
        display: none;
    }
    .section {
        padding-top: 2em;
        padding-bottom: 2em;
    }
    .section__content + .section__content {
        margin-top: 1em;
    }
    body {
        font-size: 16px;
    }
    .text {
        //55
        &-title {
            font-size: 1.75em;
            line-height: 1.13em;
        }
        //34
        &-subtitle {
            font-size: 1.375em;
        }
        //30
        &-subtitle-mini {
            font-size: 1.25em;
        }
        //24
        &-st {
            font-size: 1.125em;
        }
        //22
        &-st-mini {
            font-size: 1.125em;
        }
        //16
        &-litr {
            font-size: 0.75em;
        }
        //12
        &-litr-mini {
            font-size: 0.75em;
        }
    }

    ._1 {
        background: url(/public/1_bg.png) top right -50% / 60% no-repeat,
        #FFF2FA;
        img {
            position: absolute;
            max-width: 100px;
            width: 100%;
            top: -15%;
            right: 15%;
        }
    }
    .box {
        padding: 2em 1em;

        .grid__col._sm_12 + .grid__col._sm_12 {
            margin-top: 0em;
        }

        &.pink {
            margin-bottom: 2em;
        }
    }
    .box-mini {
        padding: 2em;
        min-height: 0 !important;
    }
    .ul-margin {
        .grid__col._sm_12 + .grid__col._sm_12 {
            margin-top: 0em;
        }
    }

    ._2 {
        .item-center {
            margin-bottom: -1em;
        }
        .box.lightblue {
            position: relative;
            img {
                margin-top: 1em;
            }
        }
    }
    ._2, ._7 {
        .box-mini {
            display: flex;
            align-items: center;
            text-align: left;
            padding: 1em;
            margin-top: 0;
            img {
                max-width: 62px;
                width: 100%;
                height: auto;
                margin: 0;
                margin-right: 1em;
            }
        }
    }
    ._3 {
        img {
            margin-top: 2em;
            margin-bottom: 1em;
        }
    }
    ._4 {
        .box-mini {
            padding: 1.5em;
        }
        .box.lightblue {
            margin-top: 3em;
            padding: 2em 1em;
        }
    }
    ._5 {
        .text-subtitle.margin-t {
            padding-right: 0em;
        }
        .box.pink {
            margin-bottom: 0;
            .text-st {
                padding: 0em;
            }
            img {
                margin-top: 1em;
                margin-bottom: 0.5em;
            }
        }
    }
    ._6 {
        .box.lightblue {
            margin-top: 3em;
            margin-bottom: 3em;
            padding: 2em 1em 1em;
            img {
                margin-bottom: 1em;
            }
        }
    }
    ._7 {
        background: url(../../public/7_bg.png) top right 220%/120% no-repeat, #FFF2FA;
    }
    ._8 {
        a {
            margin: auto;
        }
        img {
            margin: 1em auto;
        }
        ._sm_8 {
            margin-top: 2em;
        }
        .text-litr-mini {
            text-align: center;
        }
    }
}
.icon-animated { 
    animation: vibration 1s 0s infinite both; 
} 
@keyframes vibration { 
    0% { 
        transform: scale(1) 
    } 
    25% { 
        transform: scale(1.05) 
    } 
    50% { 
        transform: scale(1) 
    } 
    75% { 
        transform: scale(1.1) 
    } 
    100% { 
        transform: scale(1) 
    } 
}
.icon-animated-slow { 
    animation: beating 2s 1s infinite both; 
} 
@keyframes beating { 
    0% { 
        transform: scale(1) 
    } 
    50% { 
        transform: scale(1.1) 
    } 
    100% { 
        transform: scale(1) 
    } 
}
